import styles from "./Bios.module.scss";

export default function Bios({ biosData }) {
  return (
    <>
      <div className={styles.biosTop}>
        <h3 className={styles.title}>Player Info</h3>
        {biosData.map((bio) => {
          <p>{bio.name}</p>;
        })}
      </div>
      ;
    </>
  );
}
